@charset "utf-8";

nav ul a{
	color:#333;
}

.members-home{
     padding: 0;
  margin: 0;
  list-style: none;	
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
  justify-content: space-around;
}

.members-home div{
	  padding: 5px;
  width: 200px;
  height: 150px;
  margin-top: 10px;
}

i.work {
	position: relative;
	top: 6px;
}

.card-logo{
	height: 72px;
}

.card-logo img{
	max-height:95px;
}

.card-image img{
	padding: 5% 0;
}

.padding{
	padding:24px;
}

.entry-text{
	font-size: 1.4rem;
	line-height: 150%;
	margin: .5rem 0 .4rem 0;
	color: #888;
	font-weight: 300;
}

.locks {
	background-image: url(../assets/images/locks-pattern.png);
	background-repeat: repeat;
}
.z-index{
	z-index: 1000;
	position:relative;
	left:-20%;
}

.umbrella-card{
	padding-top: 2rem;
	position: relative;
	top: -4rem;
	font-size:1.2rem;
}

.valign-shift{
	position:relative;
	top:1rem;
}

.display {
	font-size:1.1rem;
}

.header-text{
	font-size:1.5rem;
}

h1, h2, h3, h4, h5, h6 {
    -webkit-font-smoothing: antialiased;
}
.icon-block {
  padding: 0 15px;
}
.icon-block .material-icons {
	font-size: inherit;
}
.shadow-demo {
    background-color: #26a69a;
    width: 100px;
    height: 100px;
    margin: 20px auto;
}
h1.page-title {
    font-weight: 500;
    font-size: 2.3em;
    /*text-align: center;*/
}
.col.grid-example {
    border: 1px solid #eee;
    margin: 7px 0;
    text-align: center;
    line-height: 50px;
    font-size: 28px;
    background-color: tomato;
    color: white;
    padding: 0;
}
.collection .collection-item.avatar {
    padding-right: 30px;
}
.last-post {
    padding: 10px 0 15px;
}
.last-post .collection .collection-item.avatar>.date-post {
    position: absolute;
    width: 54px;
    height: 54px;
    overflow: hidden;
    left: 10px;
    display: inline-block;
    padding: 16px 0;
    vertical-align: middle;
    background: #ad1457;
    color: #fff;
    text-align: center;
}
.txt-center {
    text-align: center;
}
.post-header h1.post-title {
    color: #29b6f6;
    font-size: 2.8em;
    font-weight: 800;
}
.post-header time {
    color: #cacaca;
    font-size: 0.9em;
}
p.rss-subscribe {
    margin: 0;
}

.lists{
	margin-left:5%;
	width:85%;
}

.lists>li{
	margin-bottom:4%;
	
}
